import { atom, useAtom } from 'jotai'
import { useQuery } from '@tanstack/react-query'
import { useEffect } from 'react'
import { apiUrl, baseUrl, apiTimeout, } from '@/utils'
import axios, { AxiosInstance } from 'axios'


export const ajaxNonceAtom = atom('')

const api: AxiosInstance = axios.create({
	baseURL: baseUrl,
	timeout: parseInt(apiTimeout, 10),
	headers: {
		'Content-Type': 'application/json',
	},
})

export const useAjaxNonce = () => {
	const [
		ajaxNonce,
		setAjaxNonce,
	] = useAtom(ajaxNonceAtom)

	const { data, isLoading } = useQuery({
		queryKey: [
			'get_ajax_nonce',
		],
		queryFn: () => api.get(`${apiUrl}/wrp/ajaxnonce`),
		staleTime: 1000 * 60 * 60 * 18,
		cacheTime: 1000 * 60 * 60 * 18,
		refetchInterval: 1000 * 60 * 60 * 18,
	})

	useEffect(() => {
		if (!isLoading) {
			const nonce = data?.data || ''

			setAjaxNonce(nonce)
		}
	}, [isLoading])

	return ajaxNonce
}
